export const urlvalidation = (data) => {
    const regex = new RegExp(/[(http(s)?):\/\/(www\.)?a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/);
    console.log("valid url ", regex.test(data));
    return regex.test(data);
}


